*, *::after, *::before{
	box-sizing: border-box;
}

a{
  text-decoration: none;
  color: inherit;
}

body{
	height: 300vh;
}

:root {
	--light-green: #dff6df;
	--green: #aae7aa;
	--gray: #adadad;
}

body {
	text-align: center;
	font-family: 'roboto';
}

h1, h2, h3, h4 {
	font-family: 'italiana';
}
.white-overlay::before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: rgba(255, 255, 255, 0.25);
}
.white-overlay--dense::before {
	background-color: rgba(255, 255, 255, 0.56);
}
.write-input{
	/* background-color: gray; */
	border: none;
	padding: 5px;
	border-radius: 8px;
}
